    created() {
      let username = "立即登录";
      let headimg = "tx.png"
      if (this.$store.state.userData) {
        username = this.$store.state.userData.nickName;
        headimg = this.$store.state.userData.headIcon
      }
      this.username = username
      this.headimg = headimg
    },
    methods: {
      go() {
        this.$router.push("/login")
      }
    }
  }
